.input-group.actions {
  display: flex;
  align-items: center;
  justify-content: center;
}

/* ======= FORM ======= */
#form {
  max-width: 500px;
}
#form h2 {
  margin-top: 0;
}
#form form input {
  border: none;
  border-radius: 0.2rem;
  padding: 0.8rem;
  width: 100%;
}

.input-group {
  margin-top: 0.8rem;
}
.input-group small {
  opacity: 0.5;
}
.input-group.actions {
  justify-content: space-between;
}
.input-group.button,
.input-group button {
  width: 48%;
}